.my-modal-backDrop {
    background-color: transparent;
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    top: 0;
    z-index: 999;
    opacity: 1;
}
.my-modal {
    z-index: 9999;
    background-color: #fff;
    opacity: 1;
    margin-top: 8%;
    margin-right: 20%;
    margin-left: 20%;
    max-height: 80%;
    overflow-y: auto;
    box-shadow: 0px 0px 48px 0px black;
    padding: 10px;
    border: 1px solid grey;
    border-top: 10px solid #a07e49;
    border-radius: 5px;
    /* -webkit-transform: translate(-50%, -200%);
    -ms-transform: translate(-50%, -200%); */
    /* transform: translate(0%, -500%); */
    transition: transform 2000ms ease-out;
}
.open-modal {
    transform: translate(0%, 50%);
}
.close-modal {
    transform: translate(0%, -500%);
}
.open-modal-container {
    display: block;
}
.close-modal-container {
    display: none;
}
.my-modal-title {
    font-size: 35px;
    color: #8a714b;
    background: #f1f1f1;
    padding-left: 10px;
}
.my-modal-body {
    margin: 5px;
    padding: 5px;
}
.my-modal-footer {
    margin: 5px;
    padding: 5px;
}
/* Modal here */
.modal {
    top: 10% !important;
    z-index: 9999;
    transform: translate(0%, -520%);
    transition: transform 2000ms ease-out;
}

.open, .animate-in {
    display: block;
    transform: translate(0%, 20%);
    transition: transform 3000ms ease-out;
}
.close-window, .animate-out {
    display: block;
    transform: translate(0%, -500%);
    transition: transform 2000ms ease-out;
}

.close-icon {
    font-size: 39px;
    margin-top: -54px;
    color: #a56204;
    cursor: pointer;
}
